import React from 'react';
import { Link } from 'gatsby';
import Layout from '../components/Layout/Layout';
import err from '../img/err.png';

function ErrorPage() {
	return (
		<Layout title="خطا | صفحه مورد نظر یافت نشد">
			<div style={{ marginTop: '13em', paddingBottom: '13em' }}>
				<div className="d-flex justify-content-center">
					<img alt="errimg" src={err} />
				</div>
				<p className="text-center m-0 p-0 mt-2" style={{ color: '#9e9e9e' }}>
					خطا صفحه مورد نظر یافت نشد
				</p>
				<p className="text-center m-0 p-0 mt-2" style={{ color: '#9e9e9e' }}>
					<Link className="m-2" to="/shop">
						محصولات
					</Link>
					|
					<Link className="m-2" to="/blog">
						مطالب
					</Link>
				</p>
			</div>
		</Layout>
	);
}

export default ErrorPage;
